import React, { Component } from 'react';

import Toilet from '../Components/Toilet';
import StatusToggle from '../Components/StatusToggle';
import Timer from '../Components/Timer';
import Options from '../Models/Options';


export default class MainContainer extends Component {

    constructor() {
        super();
        this.state = {
            toilets: [],
        }
        this.socket = new WebSocket("ws://localhost:8080/IvenHome/actions");
        this.onStatusToggle = this.onStatusToggle.bind(this);
    }
    componentWillMount() {
        this.connectToServer.call(this);
    }

    render() {
        return (
            <div className="toilets">
                    {this.generateToilets.call(this)}
            </div>
        );
    }

    connectToServer() {
        
        this.socket.onmessage = (event) => {
            let toilets = [];
            try {
                toilets = JSON.parse(event.data);
            } catch (e) {
                throw new Error('Toilet data not parsable!');
            }
            this.setState( () => {
                return {toilets}
            });
        }
    }
    
    generateToilets(){
        return this.state.toilets.map( toilet => {
            return <div className="toilet-container" key={toilet.id}>
                    <Toilet toilet={toilet}/>
                    { this.props.isAdmin && <StatusToggle value={toilet.status} toiletId={toilet.id} options={Options} onChange={this.onStatusToggle}/>}
                    { toilet.status === 'Occupied' && <Timer startTime={toilet.toggleTime}/>}
                </div>
      });
    }
    onStatusToggle(propsPassed, event){
        var payLoad = {
            action: "toggle",
            id: propsPassed.toiletId,
            newStatus: event.target.value
        };
        this.socket.send(JSON.stringify(payLoad));
    }
}



