import React from 'react';

const Toilet = ({toilet}) => {
    return <div id="Toilet">
        <div className="toilet-name">{toilet.name}</div>
        <div className="toilet-id" hidden>{toilet.id}</div>
        <div className="toilet-status">{toilet.status}</div>
    </div>
}

export default Toilet;


